

.timer__items {
  color: black;
  display: flex;
  font-size: 48px;
  max-width: max-content;
  margin: 0 auto;
  margin-top: 70px;
}

.timer__box {
  position: relative;
  width: 200px;
  height: 200px;
  border: solid 3px rgb(100, 97, 114);
  border-radius: 50% 50%;

  &:not(:last-child) {
    margin-right: 10px;
  }
}

.timer__item {
  position: relative;
  min-width: 60px;

  padding-bottom: 15px;
  text-align: center;
  position: relative;
  width: 100%;
  height: 100%;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.timer__item::before {
  content: attr(data-title);
  display: block;
  position: absolute;
  left: 50%;
  bottom: 30px;
  transform: translateX(-50%);
  font-size: 14px;
}

canvas {
  position: absolute;
  top: 0px;
  left: 0px;
}

.timer-button-wrapper {
  margin-top: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  
}

.timer-button-wrapper button {
  padding: 10px 20px;
  font-size: 28px;
}