.tags {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 8px;
  background-color: #fff;
  list-style: none;
  user-select: none;
  outline: 1px solid tomato;
}

.tags__item {
  margin: 4px;
}

.tags__btn {
  border: 1px solid #212121;
  border-radius: 4px;
  display: inline-block;
  padding: 4px 10px 4px;

  font-size: 24px;
  font-family: inherit;
  color: #212121;
  font-weight: 500;
  text-align: center;
  white-space: nowrap;
  text-decoration: none;
  outline: none;

  background-color: #fff;
  cursor: pointer;
}

.tags__btn--active {
  color: #fff;
  background-color: #3f51b5;
}